body {
  font-family: helvetica;
  margin: 0;
  overflow: hidden;
}

/* Page Content */

#content-wrapper {
  width: 1200px;
  margin: 0 auto;
  min-height: 75vh;
}

#content-internal-wrapper {
  width: 100%;
  padding: 35px;
}

#disclaimer {
  width: 100%;
  height: auto;
  background: black;
}

#disclaimer-internal {
  width: 1200px;
  margin: 0 auto;
}

#disclaimer {
  span {
    color: white;
    display: inline-block;
    font-size: 14px;
    padding: 10px;
  }

  i {
    color: white;
    display: inline-block;
    font-size: 16px;
    padding: 10px 0 10px 35px;
  }

  a {
    color: #efefef;
    text-decoration: underline;
  }
}

/* Navigation Bar */

nav {
  overflow: hidden;
  width: 1200px;
  margin: 0 auto;
  height: auto;
}

#nav-wrapper {
  width: 100%;
  margin: 0;
  background: #202020;
}

#nav-internal-wrapper {
  width: 100%;
  margin: 0 auto;
  padding: 0 35px;
}

#nav-topper {
  width: 100%;
  margin: 0 auto;
  position: relative;
}

#nav-logo {
  width: 200px;
  padding: 20px 0 20px 0;
  height: auto !important;
}

#nav-account {
  position: absolute;
  right: 0;
  top: 35px;
  text-align: right;

  a {
    display: inline-block;
    overflow: hidden;
    box-sizing: border-box;
    -moz-box-sizing: border-box;
    text-decoration: none;
  }

  .nav-logout {
    font: inherit;
    vertical-align: top;
    display: inline-block;
    margin: 0;
    padding: 0;
  }

  button {
    cursor: pointer;
    border: none;
    padding: 6px 12px 4px 12px;
    margin-left: 25px;
    border-radius: 5px;
    border: solid .1em white;
    font: inherit;
    font-size: 18px;
    box-sizing: border-box;
    -moz-box-sizing: border-box;
    appearance: none;
    background: transparent;
    color: white;
    display: block;
    transition: background ease .35s;

    &:focus {
      outline: none;
      outline: none;
      background: #2e5945;
    }

    &:hover {
      outline: none;
      background: #2e5945;
    }
  }
}

#nav-buttons {
  width: 100%;
  margin: 0 auto 5px auto;
  text-align: left;

  li {
    display: inline-block;
    overflow: hidden;
  }

  a {
    display: block;
    color: white;
    font-size: 18px;
    padding: 10px 0;
    padding-right: 50px;
  }

  .active {
    color: white;
  }

  a {
    &:hover, &:focus {
      color: white;
    }
  }

  .divider {
    margin: .8vw 0;
    background: black;
    width: .05vw;
    min-width: 1px;
    height: calc(12px + .3vw);
    min-height: 12px;
  }
}

.nav-logout button {
  cursor: pointer;
  border: none;
  padding: 6px 12px 4px 12px;
  margin-left: 25px;
  border-radius: 5px;
  border: solid .1em white;
  font: inherit;
  font-size: 18px;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  appearance: none;
  background: transparent;
  color: white;
  display: block;
  transition: background ease .35s;
  border-color: white;
  color: white;

  &:focus {
    outline: none;
  }
}

/* Footer */

footer {
  overflow: hidden;
  width: 1200px;
  margin: 0 auto;
  height: auto;
}

#footer-wrapper {
  width: 100%;
  margin: 0;
  background: #214031;
}

#footer-internal-wrapper {
  width: 100%;
  margin: 0 auto;
  padding: 35px 35px 0 35px;
}

.footer-column {
  display: inline-block;
  width: 100%;
  text-align: center;

  h3 {
    font-size: 24px;
  }
}

#footer-bottom {
  width: 100%;
  text-align: center;

  a {
    text-decoration: none;
    display: inline-block;
    font-size: 15px;
    color: white;
    padding: 20px 5px;
  }
}

#footer-logo-wrapper {
  display: block;
  width: auto;
  height: auto;
  margin: 0;
  padding: 0;
}

#footer-logo {
  width: 120px;
  padding: 35px 0 20px 0;
  height: auto !important;
}

.footer-column i {
  color: #efefef;
  display: inline-block;
  font-size: 25px;
  padding: 5px 8px;
}

#footer-copyright {
  width: 100%;
  text-align: center;

  a {
    display: block;
    color: #d9af62;
    font-size: 12px;
    padding-bottom: 15px;
  }
}

/* HOMEPAGE - Compress and Move for Production*/

.homepage-center-column {
  box-sizing: border-box;
  vertical-align: top;
  display: inline-block;
  width: 74%;
  height: auto;
}

.homepage-right-column {
  float: right;
  vertical-align: top;
  box-sizing: border-box;
  display: inline-block;
  width: 25%;
  height: auto;
}

#homepage-tournaments {
  display: inline-block;
  width: 100%;
  height: 30vh;
  border: solid 1px #efefef;
  vertical-align: top;
  box-sizing: border-box;
  margin: 0;
}

#homepage-scoreboard {
  display: inline-block;
  width: 100%;
  height: 30vh;
  border: solid 1px #efefef;
  margin-bottom: 10px;
}

#homepage-kegal {
  display: inline-block;
  width: 100%;
  height: auto;
  border: solid 1px #efefef;
}

.homepage-section-title {
  width: auto;
  margin: 15px 15px 0 15px;

  a {
    font-size: 18px;
    color: #000;
  }

  div {
    width: 100%;
    height: 1px;
    background: #000;
  }

  .homepage-section-title-view {
    float: right;
    font-size: 14px;
    color: #3d3d3d;

    i {
      font-size: 12px;
      color: #3d3d3d;
    }
  }
}

.homepage-kegal-text {
  color: #3d3d3d;
  font-size: 15px;
  padding: 15px;
  display: block;
}

.homepage-kegal-logo-wrapper {
  width: 100%;
  height: auto;

  img {
    display: block;
    width: 80%;
    height: auto !important;
    margin: 0 auto 20px auto;
  }
}